@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.outside {
  text-align: center;
}
.btn {
  display: inline-block;
  max-width: 20.9375rem;
  width: 100%;
  max-height: 3.125rem;
  height: 100%;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.45;
  background-color: #333;
  color: #fff;
  border: solid 1px #fff;
  overflow: hidden;
  position: relative;
  padding: 0;
}
.btn::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transition: 0.1s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%, 0%);
}
.btn:hover::before {
  transform: translate(0%, 0%);
  transition: 0.1s;
}
.btn:hover span {
  position: relative;
  z-index: 10;
  color: #333;
  transition: 0.1s;
}
.contact__btn {
  padding-top: 0.625rem;
  padding-right: 0rem;
  padding-bottom: 0.6875rem;
  padding-left: 0rem;
}
.contact__inner {
  display: inline-block;
  max-width: 23.4375rem;
  width: 100%;
  background-color: #3d3d3d;
  padding-top: 4.1875rem;
  padding-right: 1.1875rem;
  padding-bottom: 4rem;
  padding-left: 1.1875rem;
}
@media screen and (min-width: 768px) {
  .contact__inner {
    display: block;
    max-width: 80rem;
    width: 100%;
    margin: 0 auto;
    padding-top: 9.25rem;
    padding-right: 0rem;
    padding-bottom: 9.3125rem;
    padding-left: 0rem;
  }
}
.contact__text--inner {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.4375;
  color: #fff;
  padding-bottom: 2.5rem;
}
@media screen and (min-width: 768px) {
  .contact__text--inner {
    padding-bottom: 3.8125rem;
  }
}
.contact__title--inner {
  width: 100%;
  height: 100%;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.45;
  color: #fff;
  padding-bottom: 1.25rem;
}
@media screen and (min-width: 768px) {
  .contact__title--inner {
    font-size: 1.875rem;
    letter-spacing: 0.125rem;
    line-height: 1.1666;
    position: relative;
    padding-bottom: 1.5rem;
  }
}
.contact__title--inner::before {
  content: "Contact";
  display: block;
  font-family: "Noto Serif JP", serif;
  font-size: 3.125rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.44;
  color: #f9f871;
  opacity: 0.27;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 26%);
}
@media screen and (min-width: 768px) {
  .contact__title--inner::before {
    font-size: 6.875rem;
    letter-spacing: 0.125rem;
    line-height: 1.4363;
    top: 0;
    left: 0;
    transform: translate(25%, -61%);
  }
}